                            }
                        }
                        .right {
                            font-size: 15px;
                        }
                    }
                    .good {
                        .good_img {
                            width: 75px;
                            height: 75px;
                            img {
                                width: 75px;
                                height: 75px;
                            }
                        }
                        .good_info {
                            width: 260px;
                            .good_name {
                                font-size: 17px;
                                height: 44px;
                            }
                            .good_dis {
                                width: 100%;
                                display: flex;
                                justify-content: space-between;
                                align-items: center;
                                margin-top: 4px;
                                .good_unit {
                                    font-size: 14px;
                                }
                                .good_price {
                                    font-size: 14px;
                                    span:last-child{
                                        font-size: 20px;
                                    }
                                }
                            }
                        }
                    }
                }
            }
            .order_info {
                width: 100%;
                .item {
                    .left {
                        font-size: 17px;
                    }
                    .right {
                        font-size: 17px;
                        .billNo{
                            font-size: 15px;
                        }
                        .copy {
                            font-size: 14px;
                        }
                    }
                }
            }
            .photo_box {
                width: 100%;
                margin: 14px auto;
                .title {
                    font-size: 17px;
                }
            }
        }
        .contact {
            .left {
                span{
                    font-size: 15px;
                }
            }
        }
    }
