            }
          }
          .good_info {
            width: 230px;
            margin-left: 10px;
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: flex-start;
            .good_name {
              font-family: PingFangSC-Medium;
              font-weight: bold;
              font-size: 14px;
              color: #333333;
              overflow: hidden;
              text-overflow: ellipsis;
              display: -webkit-box;
              -webkit-line-clamp: 2;
              -webkit-box-orient: vertical;
              width: 160px;
              line-height: 22px;
            }
            .good_dis {
              width: 100%;
              display: flex;
              justify-content: space-between;
              align-items: center;
              margin-top: 4px;
              .good_unit {
                display: flex;
                justify-content: flex-start;
                align-items: center;
                font-family: PingFangSC-Regular;
                font-size: 12px;
                color: #333333;
                .unit {
                  max-width: 135px;
                  overflow: hidden;
                  white-space: nowrap;
                  text-overflow: ellipsis;
                }
                .num {
                  color: #9a9a9a;
                  margin-left: 2px;
                }
              }
              .good_price {
                font-family: PingFangSC-Medium;
                font-size: 12px;
                color: #f43d3d;
              }
