          }
        }
      }
    }
    .order_info {
      width: 100%;
      margin: 14px auto;
      background: #ffffff;
      box-shadow: 0 0 6px 0 rgba(219, 219, 219, 0.5);
      border-radius: 4px;
      padding: 15px;
      box-sizing: border-box;
      .item {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        min-height: 20px;
        line-height: 20px;
        margin-bottom: 10px;
        .left {
          font-family: PingFangSC-Regular;
          font-size: 14px;
          color: #9a9a9a;
        }
        .right {
          display: flex;
          justify-content: flex-end;
          align-items: center;
          font-size: 14px;
          color: #333333;
          max-width: 240px;
          text-align: left;
            .billNo{
                height: 20px;
                line-height: 20px;
            }
          .copy {
            font-weight: normal;
            width: 52px;
            height: 20px;
            border: 1px solid #666666;
            border-radius: 14.5px;
            box-sizing: border-box;
            margin-left: 6px;
            font-size: 12px;
            color: #666666;
              display: flex;
              align-items: center;
              justify-content: center;
            /*line-height: 20px;*/
          }
        }
